import React, { Component } from 'react';
import Page from 'components/LayoutComponents/Page';
import BtnAddNew from 'components/common/button/BtnAddNew';
import { Card, Row, Col, message, Checkbox } from 'antd';
import { CartHeadAction, TableData, ColEditDelete } from 'components/common';
import { getPaperEnrollments, deletePaperEnrollment } from 'services/PaperEnrollment';
import { getQueryParams } from 'utils/urlUtil';
import FormSearchPaper from './FormSearchPaper';
import './styles.scss';

class ListTypePaperEnrollmentsPage extends Component {
  state = {
    reload: 0,
    paramSearch: {},
  };

  componentDidMount = () => {
    this.handleChangeQueryParams();
  };

  componentWillReceiveProps = () => {
    this.handleChangeQueryParams();
  };

  handleChangeQueryParams = () => {
    this.setState({ paramSearch: getQueryParams() });
  };

  handleDelete = async id => {
    await deletePaperEnrollment(id);
    message.success('Xóa thành công');
    this.setState({ reload: this.state.reload + 1 });
  };
  render() {
    const { reload } = this.state;
    const columns = [
      {
        title: 'Ký hiệu ',
        dataIndex: 'id',
        key: 'id',
      },

      {
        title: 'Tên giấy tờ',
        dataIndex: 'paperTypeName',
        key: 'paperTypeName',
      },
      {
        title: 'Nhóm',
        dataIndex: 'group',
        key: 'group',
      },
      {
        title: 'Bắt buộc',
        render: (text, record) => <Checkbox checked={record.isMandantory} />,
      },
      {
        title: 'Chọn mặc định',
        render: (text, record) => <Checkbox checked={record.isDefault} />,
      },
      {
        title: 'STT',
        dataIndex: 'orderNo',
        key: 'orderNo',
      },
      {
        title: 'Ghi chú',
        dataIndex: 'note',
        key: 'note',
      },
      {
        title: 'Thao tác',
        dataIndex: 'operation',
        render: (text, record) => (
          <ColEditDelete
            hrefEdit={`#type-of-paper/form/edit/${record.id}`}
            onConfirmDelete={() => this.handleDelete(record.id)}
          />
        ),
      },
    ];
    return (
      <Card className="type-of-paper" title="QĐ loại giấy tờ">
        <Row>
          <Col span={24}>
            <CartHeadAction>
              <BtnAddNew href="#/type-of-paper/form/new" />
            </CartHeadAction>
          </Col>
        </Row>
        <FormSearchPaper />
        <Row>
          <Col span={24}>
            <TableData
              reload={reload}
              paramSearch={this.state.paramSearch}
              service={getPaperEnrollments}
              columns={columns}
              rowKey="id"
            />
          </Col>
        </Row>
      </Card>
    );
  }
}

export default props => (
  <Page {...props}>
    <ListTypePaperEnrollmentsPage />
  </Page>
);



